@import '../../styles/all';

.page {
  z-index: 1000;
  background-image: url('../../../public/img/page-bg/not-found.png');

  @include on-desktop {
    position: relative;
    height: 832px;
  }

  @include on-desktop-wide {
    height: 1080px;
  }
}

.container {
  background: $color-bg;
  padding: 16px;
  
  display: flex;
  flex-direction: column;
  gap: 64px;
  align-items: center;

  @include on-desktop {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    width: 670px;
    border-radius: 32px;
    padding-block: 64px;
  }
}

.logoLink {

}

.logoIcon {
  height: 71px;

  @include on-desktop {
    height: 123px;
  }
}

.title {
  @include h1-mobile-medium;

  @include on-desktop {
    @include h2-web;
  }
}

.button {
  @include green-button;
  @include button-mobile;

  width: 100%;
  text-align: center;
  padding: 16px;
  border-radius: 1000px;
  color: $color-bg;
  text-decoration: none;

  @include on-desktop {
    @include button-web;

    width: 400px;
  }
}

